.about2 {
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
}
 
#about2-title {
    color: #10D397;
    font-size: 45px;
    text-align: center;
}
.card-container {
    width: 100%;
    height: 70%;
    padding-top: 40px;
}

 

@media only screen and (max-width: 768px) {
    #about2-title {
        color: #10D397;
        font-size: 30px;
        text-align: center;
    }
    .about2 {
        height: 200vh;
    }
    
}
@media screen and (min-device-width: 768px) and (max-device-width: 1024px) { 
    #card-cont {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .col {
        width: 70%;
    }
    #about2-title {
        color: #10D397;
        font-size: 55px;
        text-align: center;
}
.about2 {
    height: 130vh;
}
}